<template>
<transition name="move">
     <div class="food" v-show="showFlag" ref="food">
         <div class="food-content">
             <div class="image-header">
                 <img :src="food.image" alt="">
                 <div class="back" @click="hide">
                      <i class="icon-arrow_lift"></i>
                 </div>
                
             </div>
             <div class="content">
                 <h1 class="title">{{food.name}}</h1>
                 <div class="detail">
                     <span class="sell-count">月售{{food.sellCount}}份</span>
                     <span class="rating">好评率{{food.rating}}%</span>
                 </div>
                  <div class="price">
                    <span class="new">￥{{food.price}}</span><span class="oldPrice" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                 <cartcontrol  @add="_drop" :food="food"></cartcontrol>
                </div>
             <transition name="fade">
                 <div class="buy" @click="addFirst($event)" v-show="!food.count || food.count === 0">加入购物车</div>
             </transition>
             </div>
             <split></split>
            <div class="info" v-show="food.info">
                <h1 class="title">商品介绍</h1>
                <p class="text">{{food.info}}</p>
            </div>
         </div>
    </div>
</transition>
   
</template>
<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import cartcontrol from "../cartcontrol/cartcontrol";
import split from "../split/split";
import Vue from 'vue';
export default{
 props:{
    food:{
      type:Object
    }
  },
  data(){
      return {
          showFlag:false
      };
  },
  methods: {
      show(){
          this.showFlag = true;
          this.$nextTick(()=>{
              if(!this.scroll){
                  this.scroll = new BScroll(this.$refs.food,{click:true});
              }else{
                  this.scroll.refresh();
              }
          });
      },
      hide(){
          this.showFlag = false;
      },
      addFirst(event){
          if(!event._constructed){
              return;
          };
          Vue.set(this.food,'count',1);
          this.$emit('add',event.target);
      },
       _drop(target){
      // 异步执行，优化动画
      this.$emit('add',target);
    }
     
  },
  components:{
      cartcontrol,
      split
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus"> 
    .food
        position:fixed
        top:0
        left:0
        bottom:48px
        z-index:30
        width:100%
        background:#fff
        transform:translate3d(0,0,0)
        transition:all 0.2s linear
        &.move-enter,&.move-leave-to
            transform:translate3d(100%,0,0)
        .image-header
            position:relative
            width:100%
            height:0
            padding-top:100%
            img
             position:absolute
             top:0 
             left:0
             width:100%
             height:100%
            .back
                position:absolute
                top:10px
                left:0
                .icon-arrow_lift
                    display:block
                    padding:10px
                    font-size:20px
                    color:#fff
        .content
            padding:18px
            position:relative
            .title
                line-height:14px
                margin-bottom:8px
                font-size:14px
                font-weight:700
                color:rgb(7,17,27)
            .detail
                margin-bottom:18px
                line-height:10px
                height:10px
                font-size:0
                .sell-count,.rating
                    font-size:10px
                    color:rgb(147,153,159)
                .sell-count
                    margin-right:12px
            .price
                font-weight:700
                line-height:24px
                .new
                    margin-right:8px
                    font-size:14px
                    color:rgb(240,20,20)
                .oldPrice
                    font-size:10px
                    text-decoration:line-through
                    color:rgb(147,153,159)
            .cartcontrol-wrapper
                position:absolute
                right:12px
                bottom:12px
            .buy
                position:absolute
                right:18px
                bottom:18px
                z-index:10
                height:24px
                line-height:24px
                padding:0 12px
                box-sizing:border-box
                font-size:10px
                border-radius:12px
                color:#fff
                background:rgb(0,160,220)
                transition:all 0.2s linear
                opacity:1
                &.fade-enter,&.fade-leave-to
                    opacity:0
        .info
            padding:18px
            .title
                line-height:14px
                margin-bottom:6px
                font-size:14px
                color:rgb(7,17,27)
            .text
                line-height:24px
                padding:0 8px
                font-size:12px
                color:rgb(77,85,93)
</style>



